@import "../../../../styles/borders.scss";
@import "../../../../styles/shadows.scss";
.horizontal-stepper {
    .mat-horizontal-content-container {
        padding: 20px 0 !important;
    }
    .mat-horizontal-stepper-header-container {
        background: white !important;
        box-shadow: $box-shadow-3;
        z-index: 100 !important;
        height: 56px;
        display: inline-flex;
        .mat-step-header {
            height: 56px !important;
            background: white !important;
            &.cdk-focused.cdk-program-focused {
                background: white !important;
            }
            .mat-step-icon {
                background-color: var(--color-neutral-gray-20);
                border-radius: $border_radius_xs;
                opacity: 0.5 !important;
            }
            .mat-step-icon-selected {
                background-color: var(--company-color-brand-primary-base);
                opacity: 1 !important;
            }
            .mat-step-label {
                color: var(--color-neutral-gray-20);
                opacity: 0.5 !important;
            }
            .mat-step-label-selected {
                color: var(--company-color-brand-primary-base);
                opacity: 1 !important;
                background-color: white !important;
            }
        }
        .mat-horizontal-stepper-header .mat-step-icon {
            margin-right: 12px !important;
        }
        .mat-stepper-horizontal-line {
            display: none !important;
        }
        .mat-step-header:hover {
            height: 56px;
            background-color: white !important;
        }
    }
    .mat-horizontal-stepper-header-container:hover {
        height: 56px;
    }
}