.tablecell-toggle {
    margin-top: 2px;
    .mat-slide-toggle {
        .mat-slide-toggle-thumb {
            background-color: var(--color-neutral-gray-50) !important;
        }
        .mat-slide-toggle-bar {
            background-color: var(--color-neutral-gray-20) !important;
        }
    }
    .mat-checked {
        .mat-slide-toggle-thumb {
            background-color: var(--company-color-brand-primary-base) !important;
        }
    }
}