// Custom Theming for Angular Material
@use '@angular/material' as mat;
@import "../../styles/storybook.scss";
// For more information: https://material.angular.io/guide/theming
@import "../../styles/colors.scss";
@import "./variables.scss";
@import "../../styles/borders.scss";
@import "../../styles/grid.scss";
@import "../../styles/shadows.scss";
@import "../../styles/spacing.scss";
@import "../../styles/typography.scss";
@import "../../styles/radius.scss";
// @import "~bootstrap/scss/bootstrap";
// Plus imports for other components in your app.
// Include the common styles for Angular Material. We include this here so that you only
// have to load a single css file for Angular Material in your app.
// Be sure that you only ever include this mixin once!
@include mat.core();
// Define the palettes for your theme using the Material Design palettes available in palette.scss
// (imported above). For each palette, you can optionally specify a default, lighter, and darker
// hue. Available color palettes: https://material.io/design/color/
$embraer-framework-primary: mat.define-palette(mat.$indigo-palette);
$embraer-framework-accent: mat.define-palette(mat.$pink-palette, A200, A100, A400);
// The warn palette is optional (defaults to red).
$embraer-framework-warn: mat.define-palette(mat.$red-palette);
// Create the theme object (a Sass map containing all of the palettes).
$embraer-framework-theme: mat.define-light-theme( $embraer-framework-primary, $embraer-framework-accent, $embraer-framework-warn);
// Include theme styles for core and each component used in your app.
// Alternatively, you can import and @include the theme mixins for each component
// that you are using.
@include mat.all-component-themes($embraer-framework-theme);
$company: #ffff;
$prefix: "--"; //Prefix string for custom CSS properties
@function iconColor($color) {
    $color-string: "#{$color}";
    $color-sliced: str-slice($color-string, 2, str-length($color-string));
    @return $color-sliced;
}

//Merges a variable name with $prefix
@function custom-property-name($name) {
    @return $prefix+$name;
}

// Defines a custom property
@mixin define-custom-property($name, $value) {
    #{custom-property-name($name)}: $value;
}


/* You can add global styles to this file, and also import other style files */

html,
body {
    height: 100%;
}

::ng-deep * {
    font-family: Roboto, sans-serif;
}

body {
    margin: 0;
    font-family: Roboto, sans-serif;
    @include define-custom-property( "primary_lighter_70", $company_color_brand_primary_lighter_70);
}

a:link {
    color: $company_color_brand_primary_base;
    text-decoration: none;
}

a:link:hover {
    text-decoration: underline !important;
}

a:link::before {
    color: $company_color_brand_primary_base;
    text-decoration: none;
}

app-icon {
    display: -webkit-inline-flex;
}

.cdk-overlay-container {
    z-index: 1000 !important;
}

@import "./themes.scss";
