@import "../../../../styles/borders.scss";
@import "../../../../styles/shadows.scss";
.tab {
    width: 100%;
    .mat-tab-header {
        border-radius: $border_radius_sm $border_radius_sm 0px 0px;
        border-bottom: none !important;
        overflow: inherit !important;
    }
    .mat-ripple-element {
        display: none !important;
    }
    .mat-tab-label-container {
        overflow: inherit !important;
    }
    .mat-ink-bar {
        display: none !important;
    }
    .mat-tab-label {
        padding: 16px !important;
        background-color: var(--color-neutral-gray-5) !important;
        border-radius: $border_radius_sm $border_radius_sm 0px 0px;
        margin-right: 4px;
        color: var(--color-neutral-gray-60);
        opacity: 1;
        display: flex;
        align-items: center;
        min-width: 100px !important;
        .icon::before {
            font-family: "Material Icons Outlined";
            content: "\e8cb";
            color: var(--company-color-brand-primary-base) !important;
            font-size: 18px;
            font-weight: 100;
        }
    }
    .mat-tab-label-active {
        background-color: white !important;
        z-index: 1 !important;
        border: none !important;
        color: var(--company-color-brand-primary-base);
        box-shadow: $box-shadow-3;
    }
    .mat-tab-body-wrapper {
        box-shadow: $box-shadow-3 !important;
        border-radius: 0px $border_radius_sm $border_radius_sm $border_radius_sm;
        min-height: 200px;
    }
    .mat-tab-body {
        background-color: white;
        z-index: 2;
    }
    .mat-tab-body-active {
        z-index: 3 !important;
    }
}