ngb-pagination {
    .pagination {
        display: flex !important;
        padding-left: 0 !important;
        list-style: none !important;
        border-radius: 0.25rem !important;
    }
    .page-link {
        position: relative;
        display: block;
        padding: 3px 7px;
        line-height: 1;
        color: var(--color-neutral-gray-50);
        background-color: #fff;
        text-decoration: none;
        border: none !important;
        border-radius: 4px;
        box-shadow: none !important;
        font-size: medium;
    }
    ol,
    ul,
    dl {
        margin-top: 0 !important;
        margin-bottom: 1rem !important;
    }
    *,
    *::before,
    *::after {
        box-sizing: border-box;
    }
    ul {
        display: block !important;
        list-style-type: disc !important;
        margin-block-start: 1em !important;
        margin-block-end: 1em !important;
        margin-inline-start: 0px !important;
        margin-inline-end: 0px !important;
        padding-inline-start: 40px !important;
    }
}

.active {
    .page-link {
        background-color: var(--company-color-brand-primary-base) !important;
        color: white;
        .sr-only {
            display: none !important;
        }
    }
}

.color {
    outline: none;
    .mat-icon {
        border: 1px solid;
        padding: 0 !important;
    }
    [_nghost-smu-c131] .mat-icon[_ngcontent-smu-c131] {
        border: 1px solid !important;
        padding: 0 !important;
    }
}

.same-time {
    [_nghost-yqf-c131] .mat-icon[_ngcontent-yqf-c131] {
        vertical-align: top;
        padding: 0px !important;
        font-size: 1.1em;
    }
}

.mat-paginator {
    .mat-paginator-range-actions {
        .mat-focus-indicator.mat-paginator-navigation-previous.mat-icon-button.mat-button-base,
        .mat-focus-indicator.mat-paginator-navigation-next.mat-icon-button.mat-button-base {
            display: none !important;
        }
    }
}

.paginator {
    .mat-paginator-container {
        display: flex;
        align-items: center;
    }
    .mat-form-field-wrapper {
        padding: 0 !important;
    }
    .mat-paginator-page-size-select {
        margin: 0 !important;
    }
    .mat-paginator-page-size {
        display: flex !important;
        align-items: center !important;
        color: var(--color-neutral-gray-80) !important;
    }
    .mat-form-field-infix {
        padding: 0 !important;
        border: none !important;
    }
    .mat-select-value {
        color: var(--color-neutral-gray-80) !important;
        font-size: 12px;
        font-weight: 500;
        text-align: right;
    }
    .mat-paginator-range-actions {
        display: none !important;
    }
    .mat-paginator-page-size-label {
        margin: 0 4px;
        min-width: 100px;
    }
    .mat-paginator-range-label {
        margin: 0 !important;
        margin-left: 32px;
        color: var(--color-neutral-gray-80) !important;
        font-size: 12px;
        font-weight: 500;
        text-align: right;
    }
    button:not(.disabled) {
        color: var(--company-color-brand-primary-base);
    }
    .mat-form-field-underline {
        display: none;
    }
    .mat-select-arrow {
        color: var(--company-color-brand-primary-base);
    }
}