$grid-gutter-small: 4px;
$grid-gutter-medium: 8px;
$grid-gutter-large: 12px;
$grid-padding-container-small: 8px;
$grid-padding-container-medium: 12px;
$grid-padding-container-large: 26px;
// Total de Colunas por Device
$grid-columns-small: 8;
$grid-columns-medium: 12;
$grid-columns-large: 12;
// Containers
$grid-container-small: 540px;
$grid-container-medium: 1140px;
$grid-container-large: 1140px;
// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
    [class*="small-col-"] {
        float: left;
        padding: 0 $grid-gutter-small;
    }
    @for $i from 1 through $grid-columns-small {
        .small-col-#{$i} {
            $w: calc(100% / $grid-columns-small) * $i;
            width: calc(#{$w} - #{2* $grid-gutter-small}) !important;
        }
    }
    .container {
        max-width: $grid-container-small;
        padding: 0 $grid-padding-container-small !important;
    }
}

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 1199.98px) {
    [class*="medium-col-"] {
        float: left;
        padding: 0 $grid-gutter-medium;
    }
    @for $i from 1 through $grid-columns-medium {
        .medium-col-#{$i} {
            $w: calc(100% / $grid-columns-medium) * $i;
            width: calc(#{$w} - #{2* $grid-gutter-medium}) !important;
        }
    }
    .container {
        max-width: $grid-container-medium;
        padding: 0 $grid-padding-container-medium !important;
    }
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
    [class*="large-col-"] {
        float: left;
        padding: 0 $grid-gutter-large;
    }
    @for $i from 1 through $grid-columns-large {
        .large-col-#{$i} {
            $w: calc(100% / $grid-columns-large) * $i;
            width: calc(#{$w} - #{2* $grid-gutter-large}) !important;
        }
    }
    .container {
        max-width: $grid-container-small;
        padding: 0 $grid-padding-container-small !important;
    }
}

[class*="col-"] {
    float: left;
    padding: 0 $grid-gutter-large;
}

@for $i from 1 through $grid-columns-large {
    .col-#{$i} {
        $w: calc(100% / $grid-columns-large) * $i;
        width: calc(#{$w} - #{2* $grid-gutter-large}) !important;
    }
}

.container {
    max-width: $grid-container-large;
    padding: 0 $grid-padding-container-large !important;
}

.form-row {
    flex-wrap: wrap;
    margin-right: -12px !important;
    margin-left: -12px !important;
}

.form-group {
    margin-bottom: 12px !important;
}

.d-flex {
    display: flex !important;
}

.justify-content-between {
    -webkit-box-pack: justify!important;
    -ms-flex-pack: justify!important;
    justify-content: space-between!important;
}