/* You can add global styles to this file, and also import other style files */
@import 'material-icons/iconfont/material-icons.css';
.form-field .mat-form-field-wrapper {
  padding: 0 !important;
}
.form-field .mat-form-field-flex {
  padding: 2px 12px !important;
  display: flex !important;
  align-items: center !important;
  background-color: var(--color-neutral-gray-5) !important;
  border: 1px solid var(--color-neutral-gray-20);
  border-radius: 4px !important;
}
.form-field .mat-form-field-flex::placeholder {
  color: var(--color-neutral-gray-30) !important;
  font-size: 14px;
}
.form-field .mat-input {
  margin: 0 !important;
  padding: 2px !important;
}
.form-field .mat-form-field-underline {
  display: none !important;
}
.form-field .mat-form-field-infix {
  padding: 0 !important;
  border: none !important;
}
.form-field .mat-input-element {
  color: var(--color-neutral-gray-30) !important;
  font-size: 14px;
}
.form-field mat-icon {
  margin: 0 !important;
  margin-left: auto !important;
  color: var(--company-color-brand-primary-base) !important;
}

.mat-autocomplete-visible .mat-option {
  background-color: var(--color-neutral-gray-5) !important;
  color: var(--color-neutral-gray-50) !important;
  font-style: normal;
  font-weight: 400;
  font-size: 0.875rem !important;
  line-height: 1.3125rem !important;
  padding: 12px;
}
.mat-autocomplete-visible .mat-option:hover {
  background-color: var(--company-color-brand-primary-base) !important;
  color: white !important;
}

.horizontal .list_item div.arrow app-icon mat-icon {
  top: 2px !important;
}

.button-ghost .button-content app-icon span {
  position: relative;
  top: 2px;
  font-size: 12px;
}
.button-ghost.button-small .button-content app-icon span {
  position: relative;
  top: 1.5px;
  font-size: 10px;
}
.button-ghost.button-medium .button-content app-icon span {
  position: relative;
  top: 2px;
  font-size: 12px;
}
.button-ghost.button-large .button-content {
  max-height: 18px;
}
.button-ghost.button-large .button-content app-icon span {
  position: relative;
  top: 3px;
  font-size: 15px;
}

.add .add.button-primary {
  padding: 7.06px 8px;
}
.add .add.button-secondary {
  padding: 5.06px 6px;
}
.add .add.button-ghost {
  padding: 7.06px 8px;
}
.add app-icon.ml-3 {
  margin-left: 0px !important;
}
.add app-icon span {
  font-size: 21px !important;
}

.mat-button-focus-overlay {
  display: none !important;
}

.mat-ripple-element {
  display: none !important;
}

app-icon {
  font-size: 14px;
  position: relative;
  bottom: 0px;
}

.box-shadow-5 {
  box-shadow: 0px 24px 48px 0px rgba(0, 0, 0, 0.13) !important;
}

.box-shadow-4 {
  box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.1) !important;
}

.box-shadow-3 {
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.1) !important;
}

.box-shadow-2 {
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.25) !important;
}

.box-shadow-1 {
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.25) !important;
}

.great-card {
  max-width: 400px;
  box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.1) !important;
  border-color: white !important;
  border-radius: 8px;
}

.mat-chip {
  color: var(--color-neutral-gray-80) !important;
  font-weight: 400 !important;
}
.mat-chip .mat-icon {
  color: var(--color-neutral-gray-30) !important;
}

