.alert>.close {
    outline: none !important;
    opacity: 1;
}

.alert-primary.close {
    color: var(--color-neutral-gray-80) !important;
    opacity: 1;
}

.alert-warning {
    .close {
        color: var(--color-system-yellow-darker-50) !important;
        opacity: 1;
    }
}

.alert-danger {
    .close {
        color: var(--color-system-red-darker-50) !important;
        opacity: 1;
    }
}

.alert-success {
    .close {
        color: var(--color-system-green-darker-50) !important;
        opacity: 1;
    }
}

ngb-alert {
    display: block;
}

button:not(:disabled),
[type="button"]:not(:disabled),
[type="reset"]:not(:disabled),
[type="submit"]:not(:disabled) {
    cursor: pointer;
}

.close:hover {
    text-decoration: underline;
}

.alert-dismissible .close {
    position: absolute;
    top: 2px;
    right: 0;
    padding: 16px;
    color: inherit;
}

button.close {
    padding: 0;
    background-color: transparent;
    border: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    opacity: 1;
    appearance: none;
}

.close {
    float: right;
    font-style: normal;
    font-weight: 500;
    font-size: calc(14 / 16)+rem;
    line-height: calc(14 / 16)+rem;
    opacity: 1;
}