@import "../../../../styles/borders.scss";
.mat-form-field-underline {
    display: none !important;
}

.is-invalid {
    .textinput-container .mat-form-field-flex {
        border: solid 1px var(--color-system-error-base) !important;
        color: var(--color-system-red-lighter-30) !important;
        background-color: var(--color-system-red-lighter-90) !important;
        .text {
            color: var(--color-system-error-base) !important;
        }
        input {
            background-color: var(--color-system-red-lighter-90) !important;
        }
        .warning-icon.field-text {
            font-size: 18px !important;
            display: block !important;
            color: var(--color-system-error-base) !important;
        }
        .mat-icon {
            color: var(--color-system-error-base) !important;
        }
    }
    .mat-form-field .mat-form-field-suffix {
        background-repeat: no-repeat;
        background-position: right calc(0.375em + 0.1875rem) center;
        background-size: calc(0.70em + 0.375rem) calc(0.70em + 0.375rem);
        position: relative;
        left: 9px;
        padding-left: 15px;
    }
}

.mat-form-field .mat-form-field-suffix {
    position: relative;
    left: 9px;
}

.focus {
    .mat-form-field-flex {
        border: solid 1px var(--company-color-brand-primary-darker-30) !important;
        border-radius: $border_radius_xs;
    }
}

.error-align-end {
    text-align: end;
    font-size: 12px;
    font-weight: 500;
    color: var(--color-system-error-base) !important;
}

.warning {
    .mat-form-field-flex {
        border: solid 1px var(--color-system-error-base) !important;
        background-color: var(--color-system-red-lighter-90) !important;
        .text {
            color: var(--color-system-error-base) !important;
        }
    }
}

.textinput-container {
    .mat-form-field {
        height: 40px;
    }
    .mat-form-field-flex {
        height: 40px !important;
        padding: 0px 12px !important;
        display: flex !important;
        align-items: center !important;
        border-radius: $border_radius_xs !important;
        border: solid 1px var(--color-neutral-gray-20);
    }
    .mat-form-field-infix {
        border: none !important;
        padding: 0 !important;
    }
    .mat-form-field-suffix {
        border: none !important;
        padding: 0 !important;
        .mat-input-element:disabled::placeholder {
            color: var(--color-neutral-gray-20) !important;
        }
        .visibility-disabled {
            span mat-icon {
                color: var(--color-neutral-gray-20) !important;
            }
        }
    }
    .mat-form-field-wrapper {
        padding: 0 !important;
    }
    .mat-form-field-subscript-wrapper {
        margin-top: 20px !important;
    }
    .mat-icon {
        line-height: 20px !important;
        position: relative;
        top: 3px !important;
        color: var(--company-color-brand-primary-darker-30);
    }
}