@import "../../../../styles/borders.scss";
.mat-progress-bar {
    .mat-progress-bar-buffer {
        background-color: var(--color-neutral-gray-5) !important;
    }
    .mat-progress-bar-element {
        border-radius: $border_radius_xs !important;
    }
    .mat-progress-bar-fill::after {
        border-radius: $border_radius_xs !important;
        background-color: var(--company-color-brand-primary-base) !important;
    }
}

.mat-progress-bar-element {
    border-radius: $border_radius_xs !important;
}