$spacing-xxxs: 2;
$spacing-xxs: 4;
$spacing-xs: 8;
$spacing-s: 12;
$spacing-m: 16;
$spacing-l: 24;
$spacing-xl: 32;
$spacing-xxl: 40;
$spacing-xxxl: 48;
$gaps: ( 0: 0, 1: $spacing-xxxs, 2: $spacing-xxs, 3: $spacing-xs, 4: $spacing-s, 5: $spacing-m, 6: $spacing-l, 7: $spacing-xl, 8: $spacing-xxl, 9: $spacing-xxxl);
@each $i,
$gap in $gaps {
    .m-#{$i} {
        margin: $gap+px !important;
    }
    .mb-#{$i} {
        margin-bottom: $gap+px !important;
    }
    .mt-#{$i} {
        margin-top: $gap+px !important;
    }
    .ml-#{$i} {
        margin-left: $gap+px !important;
    }
    .mr-#{$i} {
        margin-right: $gap+px !important;
    }
    .mx-#{$i} {
        margin-left: $gap+px !important;
        margin-right: $gap+px !important;
    }
    .my-#{$i} {
        margin-top: $gap+px !important;
        margin-bottom: $gap+px !important;
    }
    .p-#{$i} {
        padding: $gap+px !important;
    }
    .pb-#{$i} {
        padding-bottom: $gap+px !important;
    }
    .pt-#{$i} {
        padding-top: $gap+px !important;
    }
    .pl-#{$i} {
        padding-left: $gap+px !important;
    }
    .pr-#{$i} {
        padding-right: $gap+px !important;
    }
    .px-#{$i} {
        padding-left: $gap+px !important;
        padding-right: $gap+px !important;
    }
    .py-#{$i} {
        padding-top: $gap+px !important;
        padding-bottom: $gap+px !important;
    }
}

@mixin padding-spacing-xxxs {
    padding: 2px;
}

@mixin padding-spacing-xxs {
    padding: 4px;
}

@mixin padding-spacing-xs {
    padding: 8px;
}

@mixin padding-spacing-s {
    padding: 12px;
}

@mixin padding-spacing-m {
    padding: 16px;
}

@mixin padding-spacing-l {
    padding: 24px;
}

@mixin padding-spacing-xl {
    padding: 32px;
}

@mixin padding-spacing-xxl {
    padding: 40px;
}

@mixin padding-spacing-xxxl {
    padding: 48px;
}

.d-block {
    display: block !important;
}

.d-flex {
    display: flex !important;
}

.d-contents {
    display: contents !important;
}

.d-table {
    display: table !important;
}

.d-grid {
    display: grid !important;
}

.j-c-space-between {
    justify-content: space-between;
}