.button-ghost {
    .button-content {
        app-icon span {
            position: relative;
            top: 2px;
            font-size: 12px;
        }
    }
    &.button-small {
        .button-content {
            app-icon span {
                position: relative;
                top: 1.5px;
                font-size: 10px;
            }
        }
    }
    &.button-medium {
        .button-content {
            app-icon span {
                position: relative;
                top: 2px;
                font-size: 12px;
            }
        }
    }
    &.button-large {
        .button-content {
            max-height: 18px;
            app-icon span {
                position: relative;
                top: 3px;
                font-size: 15px;
            }
        }
    }
}

.add {
    // background-color: pink;
    .add {
        &.button-primary {
            padding: 7.06px 8px;
        }
        &.button-secondary {
            padding: 5.06px 6px;
        }
        &.button-ghost {
            padding: 7.06px 8px;
        }
    }
    app-icon {
        &.ml-3 {
            margin-left: 0px !important;
        }
        span {
            font-size: 21px !important;
        }
    }
}

.mat-button-focus-overlay {
    display: none !important;
}

.mat-ripple-element {
    display: none !important;
}

app-icon {
    font-size: 14px;
    position: relative;
    bottom: 0px;
}