$scale: 16;
// SHADOW
.button-small {
    font-style: normal;
    font-weight: 500;
    font-size: calc(12 / 16)+rem !important;
    line-height: calc(12 / 16)+rem !important;
}

.button-medium {
    font-style: normal;
    font-weight: 500;
    font-size: calc(14 / 16)+rem !important;
    line-height: calc(14 / 16)+rem !important;
}

@mixin button-medium {
    font-style: normal;
    font-weight: 500;
    font-size: calc(14 / 16)+rem !important;
    line-height: calc(14 / 16)+rem !important;
}

.button-medium {
    font-style: normal;
    font-weight: 500;
    font-size: calc(14 / 16)+rem !important;
    line-height: calc(14 / 16)+rem !important;
}

.button-large {
    font-style: normal;
    font-weight: 500;
    font-size: calc(16 / 16)+rem !important;
    line-height: calc(18 / 16)+rem !important; // O valor de 16.33 deve ser respeitado para os botões não ficarem com altura diferente a do Figma
}

// BODY
.body-01 {
    font-style: normal;
    font-weight: 400;
    font-size: calc(12 / 16)+rem !important;
    line-height: calc(18 / 16)+rem !important;
}

.body-02 {
