@import "../../../../styles/borders.scss";
.mat-checkbox {
    .mat-checkbox-inner-container {
        width: 18px;
        height: 18px;
        @include border_radius_xs;
    }
    .mat-checkbox-layout {
        display: flex;
        align-items: center;
    }
    .mat-checkbox-frame {
        border: none;
        width: 18px;
        height: 18px;
        background-color: var(--color-neutral-gray-20);
        @include border_radius_xs;
    }
    &.mat-checkbox-checked {
        .mat-checkbox-background {
            background-color: var(--company-color-brand-primary-base) !important;
            border-radius: 4px;
        }
        .mat-checkbox-checkmark {
            top: 1.4px;
            left: 1.4px;
            right: 0;
            bottom: 0;
            position: absolute;
            width: 100%;
            width: 80%;
            transform: rotate(-3deg);
            .mat-checkbox-checkmark-path {
                stroke-dashoffset: 0;
                stroke-linecap: round;
                stroke-linejoin: round;
                stroke-width: 3.133333px;
            }
        }
    }
    .mat-ripple-element {
        margin: 10px;
    }
    .mat-ripple {
        display: none !important;
    }
}

.mat-checkbox-disabled {
    .mat-checkbox-label {
        color: var(--color-neutral-gray-20) !important;
    }
    .mat-checkbox-frame {
        border: none;
        background-color: var(--color-neutral-gray-10);
    }
}