@import "../../../../styles/borders.scss";
.mat-slider {
    .mat-slider-thumb {
        display: block !important;
        transform: scale(0.6) !important;
        border: none;
        background-color: var(--company-color-brand-primary-base) !important;
    }
}

.mat-slider-thumb-label {
    border-radius: 0px;
    background-color: var(--company-color-brand-primary-base) !important;
}

.mat-slider-thumb-label-text {
    border: solid var(--company-color-brand-primary-base);
    border-width: 5px 8px;
    border-radius: 4px;
    width: 120px !important;
    line-height: 20px !important;
    padding-left: 2px !important;
    background-color: var(--company-color-brand-primary-base) !important;
}

.mat-slider-disabled .mat-slider-thumb {
    background-color: var(--company-color-brand-primary-lighter-90) !important;
    border: none;
}

.mat-slider-track-background {
    background-color: var(--color-neutral-gray-5) !important;
}

.mat-slider-track-wrapper {
    border-radius: $border_radius_sm !important;
}