@import "../../../../styles/typography.scss";
@import "../../../../styles/spacing.scss";
.menu-desktop {
    .icon {
        margin-right: 7px !important;
        display: contents;
    }
    .item {
        display: flex !important;
        min-width: 225px;
        align-items: center !important;
        color: var(--color-neutral-gray-60);
        @include body-02;
    }
    .item:hover {
        color: white !important;
        background-color: var(--company-color-brand-primary-darker-50) !important;
        .icon {
            &::after {
                // content: "\e5cc";
                content: "\ef65";
                font-family: "Material Icons";
                color: var(--color-neutral-white);
                font-size: 16px;
            }
        }
    }
    .mat-menu-item-submenu-trigger {
        &:hover::after {
            color: white !important;
        }
        &.item {
            min-width: 225px;
            &::after {
                content: "\e5cc";
                font-family: "Material Icons";
                color: var(--color-neutral-gray-60) !important;
                font-size: 16px;
                margin-left: auto;
                padding-left: 12px;
                min-width: 12px;
            }
            svg {
                display: none;
            }
        }
    }
    .mat-menu-item-highlighted {
        background-color: var(--company-color-brand-primary-darker-50) !important;
        color: var(--color-neutral-white) !important;
        .icon {
            &::after {
                content: "\ef65";
                font-family: "Material Icons";
                color: var(--color-neutral-white) !important;
                font-size: 16px;
            }
        }
        .item {
            min-width: 225px;
            color: var(--color-neutral-white) !important;
            @include body-02;
        }
        &.mat-menu-item-submenu-trigger {
            &.item {
                min-width: 225px;
                &::after {
                    content: "\e5cc";
                    font-family: "Material Icons";
                    color: var(--color-neutral-white) !important;
                    font-size: 16px;
                    margin-left: auto;
                    padding-left: 12px;
                    min-width: 12px;
                }
                svg {
                    display: none;
                }
            }
        }
    }
    .mat-menu-item-highlighted::after {
        color: var(--color-neutral-white) !important;
    }
}

.mat-menu-panel {
    position: relative;
    top: 10px;
    border-radius: 0px !important;
    .item {
        border-bottom: solid var(--color-neutral-gray-10) 1px;
        color: var(--color-neutral-gray-60);
        max-height: 50px;
        @include body-02;
        @include padding-spacing-s;
        &:last-child {
            border-bottom: solid transparent 0px !important;
        }
        &.mat-menu-item-submenu-trigger {
            &.item {
                min-width: 225px;
                &::after {
                    content: "\e5cc";
                    font-family: "Material Icons";
                    color: var(--color-neutral-gray-60);
                    font-size: 16px;
                    margin-left: auto;
                    padding-left: 12px;
                    min-width: 12px;
                }
                svg {
                    display: none;
                }
            }
        }
    }
}

.menu {
    .item {
        min-width: 225px;
        color: var(--color-neutral-gray-60) !important;
    }
    .mat-menu-item-highlighted {
        color: var(--color-neutral-white) !important;
        &::after {
            color: var(--color-neutral-white) !important;
        }
    }
}

.container {
    menu {
        display: none !important;
    }
}