@import "../../../../styles/borders.scss";
.form-field {
    .mat-form-field-wrapper {
        padding: 0 !important;
    }
    .mat-form-field-flex {
        padding: 2px 12px !important;
        display: flex !important;
        align-items: center !important;
        background-color: var(--color-neutral-gray-5) !important;
        border: 1px solid var(--color-neutral-gray-20);
        @include border_radius_xs;
    }
    .mat-form-field-flex::placeholder {
        color: var(--color-neutral-gray-30) !important;
        font-size: 14px;
    }
    .mat-input {
        margin: 0 !important;
        padding: 2px !important;
    }
    .mat-form-field-underline {
        display: none !important;
    }
    .mat-form-field-infix {
        padding: 0 !important;
        border: none !important;
    }
    .mat-input-element {
        color: var(--color-neutral-gray-30) !important;
        font-size: 14px;
    }
    mat-icon {
        margin: 0 !important;
        margin-left: auto !important;
        color: var(--company-color-brand-primary-base) !important;
    }
}

.mat-autocomplete-visible {
    .mat-option {
        background-color: var(--color-neutral-gray-5) !important;
        color: var(--color-neutral-gray-50) !important;
        font-style: normal;
        font-weight: 400;
        font-size: calc(14 / 16)+rem !important;
        line-height: calc(21 / 16)+rem !important;
        padding: 12px;
        &:hover {
            background-color: var(--company-color-brand-primary-base) !important;
            color: white !important;
        }
    }
}