.mat-radio-button.mat-accent .mat-radio-inner-circle,
.mat-radio-button.mat-accent .mat-radio-ripple .mat-ripple-element:not(.mat-radio-persistent-ripple),
.mat-radio-button.mat-accent.mat-radio-checked .mat-radio-persistent-ripple,
.mat-radio-button.mat-accent:active .mat-radio-persistent-ripple {
    background-color: var(--company-color-brand-primary-base) !important;
}

.mat-radio-label-content {
    color: var(--color-neutral-gray-80);
    font-weight: 500;
    font-size: 14px;
}

.mat-radio-checked .mat-radio-inner-circle {
    transform: scale(0.65) !important;
}

.mat-radio-outer-circle {
    background-color: var(--color-neutral-gray-20);
    border-color: var(--color-neutral-gray-20) !important;
}

.mat-ripple-element {
    display: none !important;
    background-color: var(--company-color-brand-primary-base) !important;
}

.mat-radio-disabled {
    .mat-radio-outer-circle {
        background-color: var(--color-neutral-gray-10) !important;
        border-color: var(--color-neutral-gray-10) !important;
    }
    .mat-radio-label-content {
        color: var(--color-neutral-gray-20) !important;
    }
}