.title-modal {
    margin-bottom: 12px;
    color: var(--company-color-brand-primary-base);
    &.default {
        color: var(--company-color-brand-primary-base);
    }
    &.error {
        color: var(--color-system-red-darker-30);
    }
    &.success {
        color: var(--color-system-green-darker-30);
    }
    &.warning {
        color: var(--color-system-warning-darker-50);
    }
    &.info {
        color: var(--color-system-blue-darker-30);
    }
    app-icon {
        vertical-align: sub;
        margin-right: 1px;
    }
}

::ng-deep .body-modal {
    margin-bottom: 24px;
    color: var(--color-neutral-gray-80);
}

::ng-deep .footer-modal {
    display: flex;
    align-items: center;
    justify-content: space-between;
}