.atech-overflow-menu {
    .mat-menu-content {
        padding: 0 !important;
        background-color: var(--color-neutral-gray-5);
        color: var(--color-neutral-gray-60);
        padding-top: 0px !important;
        padding-bottom: 0px !important;
    }
    .mat-menu-content:not(:empty) {
        padding-top: 0px !important;
        padding-bottom: 0px !important;
    }
    .mat-menu-item {
        height: 28px;
        padding: 0 10px !important;
        font-size: 12px !important;
        line-height: 18px;
        display: flex;
        align-items: center !important;
        mat-icon {
            font-size: 16px;
            margin-right: 8px;
            width: 15px;
            height: 16px;
        }
    }
    .mat-menu-item:hover {
        background-color: var(--company-color-brand-primary-darker-30) !important;
        color: white !important;
        mat-icon {
            color: white !important;
        }
    }
}

.mat-menu-content:not(:empty) {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
}