.table tbody.tbodyy div:not(tr div) {
    min-width: 100%;
    position: absolute;
    margin-top: -38px;
    min-height: 38px;
    cursor: pointer;
    &:hover {
        background-color: #d3d4d478;
    }
}

.table tbody.tbodyy div.actions-template.actions div {
    z-index: 999;
}

.table tbody.tbodyy div.actions-template.actions {
    display: flex;
    flex-direction: row;
    position: absolute;
}