::ng-deep .external {
    .mat-form-field-wrapper {
        padding: 0 !important;
    }
}

.progress.mat-progress-bar {
    height: 7px !important;
    border-radius: 10px;
    margin-right: 10px;
    .mat-progress-bar-fill::after {
        background-color: var(--color-system-red-base);
    }
    .mat-progress-bar-buffer {
        background-color: var(--color-neutral-gray-5) !important;
    }
    &.weak {
        .mat-progress-bar-fill::after {
            background-color: var(--color-system-red-base) !important;
        }
    }
}

.progress.good {
    .mat-progress-bar-fill::after {
        background-color: var(--color-system-yellow-base) !important;
    }
}

.progress.strong {
    .mat-progress-bar-fill::after {
        background-color: var(--color-system-green-base) !important;
    }
}