.table tbody.tbodyy div:not(tr):nth-child(even) {
    min-width: 100%;
    position: absolute;
    margin-top: -38px;
    min-height: 38px;
    cursor: pointer;
    &:hover {
        background-color: rgba(191, 192, 192, 0.28) !important;
    }
}

.table tbody.tbodyy div.actions-template.actions div {
    z-index: 999;
    position: relative;
    margin: auto;
    min-height: auto;
}

.table tbody.tbodyy div.actions-template.actions {
    display: flex;
    flex-direction: row;
    position: absolute;
}