$radius-none:0;
$radius-xs:4px;
$radius-sm:8px;
$radius-md:16px;
$circle:50%;
$pill:500px;
.radius-none {
    border-radius: $radius-none !important;
}

.radius-xs {
    border-radius: $radius-xs !important;
}

.radius-sm {
    border-radius: $radius-sm !important;
}

.radius-md {
    border-radius: $radius-md !important;
}

.circle {
    border-radius: $circle !important;
}

.pill {
    border-radius: $pill !important;
}