.tooltip.show {
    opacity: 0.9;
}

.tooltip {
    position: absolute;
    z-index: 1070;
    display: block;
    margin: 0;
    font-weight: 400;
    line-height: 1.5;
    text-align: left;
    text-align: start;
    text-decoration: none;
    text-shadow: none;
    text-transform: none;
    word-break: normal;
    word-spacing: normal;
    white-space: normal;
    line-break: auto;
    font-size: 10px;
    word-wrap: break-word;
    opacity: 0;
}

.bs-tooltip-left .arrow,
.bs-tooltip-auto[x-placement^="left"] .arrow {
    // left: 0;
    float: right;
    border-width: 0.4rem 0 0.4rem 0.4rem;
    border-left-color: var(--company-color-brand-primary-base);
}

.bs-tooltip-left {
    .tooltip-inner {
        margin-right: 4px;
        background-color: var(--company-color-brand-primary-base);
    }
    .arrow {
        right: 0;
    }
}

.bs-tooltip-top,
.bs-tooltip-auto[x-placement^="top"] {
    padding: 0.4rem 0;
}

.bs-tooltip-top .arrow,
.bs-tooltip-auto[x-placement^="top"] .arrow {
    bottom: 0;
    border-width: 0.4rem 0.4rem 0;
    border-top-color: var(--company-color-brand-primary-base);
}

ngb-tooltip-window.bs-tooltip-left .arrow,
ngb-tooltip-window.bs-tooltip-right .arrow {
    top: calc(50% - 0.4rem) !important;
}

.bs-tooltip-right {
    .arrow {
        left: 0;
        border-width: 0.4rem 0.4rem 0.4rem 0;
        border-right-color: var(--company-color-brand-primary-base);
    }
}

.bs-tooltip-bottom,
.bs-tooltip-auto[x-placement^="bottom"] {
    padding: 0.4rem 0;
}

.bs-tooltip-bottom .arrow,
.bs-tooltip-auto[x-placement^="bottom"] .arrow {
    top: 0;
    border-width: 0 0.4rem 0.4rem;
    border-bottom-color: var(--company-color-brand-primary-base);
}

.arrow {
    position: absolute;
    content: "";
    border-color: transparent;
    border-style: solid;
}

.tooltip-inner {
    max-width: 200px;
    padding: 0.25rem 0.5rem;
    color: #fff;
    text-align: center;
    background-color: var(--company-color-brand-primary-base);
    border-radius: 0.25rem;
}

.bs-tooltip-right {
    .tooltip-inner {
        margin-left: 5px !important;
    }
}