@import "../../../../styles/borders.scss";
.content-switcher {
    margin-left: 50px;
    .mat-radio-container {
        display: none !important;
    }
    .mat-radio-label-content {
        padding: 4.75px 7.34px !important;
        background-color: var(--color-neutral-gray-20);
        color: var(--color-neutral-gray-80);
        margin-left: 2.5px;
    }
    .mat-radio-checked {
        .mat-radio-label-content {
            background-color: var(--company-color-brand-primary-base);
            color: white;
        }
    }
    .last {
        .mat-radio-label-content {
            border-radius: 0 $border_radius_xs $border_radius_xs 0;
        }
    }
    .first {
        .mat-radio-label-content {
            border-radius: $border_radius_xs 0 0 $border_radius_xs;
        }
    }
}