.table {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 1rem;
    color: #767F82;
    app-checkbox {
        .mat-checkbox-label {
            font-style: normal;
            font-weight: 400;
            font-size: calc(14 / 16)+rem;
            line-height: calc(21 / 16)+rem;
        }
    }
}

.table th,
.table td {
    padding: 10px 16px 7px 16px;
    vertical-align: top;
    color: var(--color-neutral-gray-60) !important;
    font-size: 14px;
    app-checkbox {
        span {
            color: var(--color-neutral-gray-60);
        }
    }
}