.mat-card {
    .mat-card-subtitle.overline {
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 12px;
        color: var(--color-neutral-gray-80);
    }
    .mat-card-title {
        margin-bottom: 0px !important;
    }
    .mat-card-content.content-small-card {
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: var(--color-neutral-gray-80);
    }
    app-button {
        display: flex;
        justify-content: flex-start;
        margin-right: 12px !important;
    }
}