@import "../../../../../styles/borders.scss";
.vertical-stepper {
    .mat-step-header {
        padding: 20px 5px;
        .mat-step-icon {
            background-color: var(--color-neutral-gray-20);
            border-radius: $border_radius_xs;
            margin-left: 19px !important;
            z-index: 500 !important;
        }
        .mat-step-icon-selected {
            background-color: var(--company-color-brand-primary-base) !important;
        }
        .mat-step-label {
            color: var(--color-neutral-gray-20) !important;
        }
        .mat-step-label-selected {
            color: var(--company-color-brand-primary-base) !important;
        }
    }
    .mat-vertical-content-container {
        form .mat-stepper-next {
            margin-right: 12px !important;
        }
    }
    .mat-step-header:hover {
        background-color: white !important;
    }
    .cdk-program-focused {
        background-color: white !important;
    }
}