@import "../../../../styles/borders.scss";
@import "../../../../styles/shadows.scss";
button {
    outline: none;
    border: none;
    background: none;
}

button:hover {
    cursor: pointer;
}

.timepicker {
    .input {
        display: flex;
        align-items: center;
        background-color: var(--color-neutral-gray-5);
        padding: 5px 10px;
        padding-right: 0;
        border-radius: $border_radius_xs;
        border: 1px solid var(--color-neutral-gray-20);
        input {
            background-color: var(--color-neutral-gray-5);
            border: none;
            width: 100px;
            font-size: 14px;
            font-weight: normal;
            color: var(--color-neutral-gray-80);
            outline: none;
            &::placeholder {
                font-size: 14px;
                font-weight: normal;
                color: var(--color-neutral-gray-80);
            }
        }
        button {
            outline: none;
            mat-icon {
                font-size: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                color: var(--company-color-brand-primary-base);
            }
        }
    }
    .popover {
        display: flex !important;
        justify-content: flex-end !important;
        z-index: 2000 !important;
        .popover-body {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            transform: translateX(0) translateY(12px) !important;
            .container {
                background-color: white;
                padding: 16px;
                box-shadow: $box-shadow-3;
                border-radius: $border_radius_xs;
            }
        }
        ngb-timepicker {
            fieldset {
                border: none;
                padding: 0;
                color: var(--company-color-brand-primary-base);
            }
            .ngb-tp {
                button.btn-outline-primary {
                    background-color: var(--color-neutral-gray-5);
                    border: 0.8px solid var(--color-neutral-gray-20);
                    padding: 10px 12px;
                    height: 40px;
                    border-radius: $border_radius_xs;
                    color: var(--color-neutral-gray-80);
                }
            }
            .ngb-tp-input-container {
                position: relative;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                padding: 10px 0;
                padding-left: 15px;
                background-color: var(--color-neutral-gray-5);
                border: 0.8px solid var(--color-neutral-gray-20);
                border-radius: $border_radius_xs;
                .btn-link {
                    position: absolute;
                    display: flex;
                    right: 21%;
                    top: 8%;
                    outline: none !important;
                    &:first-child {
                        right: 22%;
                    }
                    &:last-child {
                        top: 44%;
                    }
                    .chevron {
                        height: 5px !important;
                        width: 5px !important;
                    }
                    .chevron::before {
                        color: var(--company-color-brand-primary-base);
                        height: 5px !important;
                        width: 5px !important;
                        border-width: 1.5px 1.5px 0px 0px !important;
                    }
                }
                .ngb-tp-input {
                    width: 50%;
                    border: none;
                    background: none;
                    text-align: left;
                    color: var(--color-neutral-gray-80);
                    font-size: 14px;
                    line-height: 14px;
                }
            }
            .sr-only {
                display: none;
            }
        }
    }
}

.change-button {
    display: block;
    justify-content: center;
    align-items: center;
    width: 100%;
    border: none;
    background-color: var(--company-color-brand-primary-base);
    border-radius: $border_radius_xs;
    padding: 12px 0px;
    color: white;
    font-size: 12px;
    font-weight: 500;
}