$box-shadow-5:0.0px 24.0px 48.0px 0px rgba(0, 0, 0, 0.13);
$box-shadow-4:0.0px 16.0px 32.0px 0px rgba(0, 0, 0, 0.10);
$box-shadow-3:0.0px 8.0px 16.0px 0px rgba(0, 0, 0, 0.10);
$box-shadow-2:0.0px 4.0px 8.0px 0px rgba(0, 0, 0, 0.25);
$box-shadow-1:0.0px 2.0px 2.0px 0px rgba(0, 0, 0, 0.25);
$base:0;
@mixin box-shadow-5 {
    box-shadow: $box-shadow-5 !important;
}

.box-shadow-5 {
    box-shadow: $box-shadow-5 !important;
}

@mixin box-shadow-4 {
    box-shadow: $box-shadow-4 !important;
}

.box-shadow-4 {
    box-shadow: $box-shadow-4 !important;
}

@mixin box-shadow-3 {
    box-shadow: $box-shadow-3 !important;
}

.box-shadow-3 {
    box-shadow: $box-shadow-3 !important;
}

@mixin box-shadow-2 {
    box-shadow: $box-shadow-2 !important;
}

.box-shadow-2 {
    box-shadow: $box-shadow-2 !important;
}

@mixin box-shadow-1 {
    box-shadow: $box-shadow-2 !important;
}

.box-shadow-1 {
    box-shadow: $box-shadow-2 !important;
}