@import "../../../../styles/shadows.scss";
@import "../../../../styles/borders.scss";
.mat-datepicker-popup {
    position: fixed !important;
}

.mat-datepicker-content {
    position: fixed !important;
    transform-origin: unset;
}

.mat-calendar {
    height: auto !important;
    position: fixed !important;
    transform: translateX(-15px) translateY(18px) !important;
    background-color: white;
    box-shadow: $box-shadow-3;
    border-radius: $border_radius_sm;
    td.mat-calendar-body-label:not(.ng-star-inserted) {
        display: none !important;
    }
    tbody {
        margin-top: 10px !important;
    }
    .ng-star-inserted {
        color: var(--company-color-brand-primary-base) !important;
        font-weight: 500 !important;
    }
    .mat-calendar-table-header-divider {
        display: none !important;
    }
    .mat-calendar-body-label {
        color: white !important;
    }
    .mat-calendar-body-selected {
        color: white !important;
        border-radius: $border_radius_xs !important;
    }
    .mat-calendar-body-cell-content:hover {
        border-radius: $border_radius_xs !important;
    }
    .mat-calendar-body-cell.mat-focus-indicator.ng-star-inserted {
        border-radius: $border_radius_xs !important;
    }
    .mat-calendar-body-cell-content {
        color: var(--color-neutral-gray-50);
        border: none !important;
    }
    .mat-calendar-period-button {
        background-color: var(--color-neutral-gray-5);
        border-radius: 4px;
    }
}

.mat-datepicker-content {
    box-shadow: none !important;
    border: none !important;
    .cdk-visually-hidden {
        display: none !important;
    }
}