$border_radius_pill:500px;
$border_radius_circle:50%;
$border_radius_md:16px;
$border_radius_sm:8px;
$border_radius_xs:4px;
$border_radius_none:0;
$border_width_none:0;
$border_width_thin:1px;
$border_width_medium:2px;
$border_width_thick:4px;
@mixin border_radius_pill {
    border-radius: $border_radius_pill !important;
}

@mixin border_radius_circle {
    border-radius: $border_radius_circle !important;
}

@mixin border_radius_md {
    border-radius: $border_radius_md !important;
}

@mixin border_radius_sm {
    border-radius: $border_radius_sm !important;
}

@mixin border_radius_xs {
    border-radius: $border_radius_xs !important;
}

@mixin border_radius_none {
    border-radius: $border_radius_none !important;
}

@mixin border_width_none {
    border-radius: $border_width_none !important;
}

@mixin border_width_thin {
    border-radius: $border_width_thin !important;
}

@mixin border_width_medium {
    border-radius: $border_width_medium !important;
}

@mixin border_width_thick {
    border-radius: $border_width_thick !important;
}