@import "../../../../styles/borders.scss";
@import "../../../../styles/shadows.scss";
button {
    outline: none;
    border: none;
    background: none;
}

button:hover {
    cursor: pointer;
}

.datetime-picker {
    .input {
        display: flex;
        align-items: center;
        background-color: var(--color-neutral-gray-5);
        padding: 5px 12px;
        border: 1px solid var(--color-neutral-gray-20);
        border-radius: $border_radius_xs;
        input {
            background-color: var(--color-neutral-gray-5);
            border: none;
            font-size: 14px;
            font-weight: normal;
            color: var(--color-neutral-gray-80);
            outline: none;
            padding: 0 1px;
            &::placeholder {
                font-size: 14px;
                font-weight: normal;
                color: var(--color-neutral-gray-80);
            }
        }
        button {
            display: flex !important;
            align-items: center !important;
            justify-content: center !important;
            mat-icon.date_time_toggle {
                background: url("data:image/svg+xml,%3Csvg%20width='18'%20height='19'%20viewBox='0%200%2016%2017'%20fill='none'%20xmlns='http://www.w3.org/2000/svg'%3E%3Cpath%20d='M12.2729%2011.1601V11.0601H12.1729H11.6543H11.5543V11.1601V13.2341V13.2908L11.6029%2013.3199L13.2449%2014.3051L13.33%2014.3562L13.3817%2014.2714L13.641%2013.8462L13.6937%2013.7597L13.6066%2013.7081L12.2729%2012.9179V11.1601ZM12%209.67734C10.2268%209.67734%208.78892%2011.1152%208.78892%2012.8885C8.78892%2014.6616%2010.2232%2016.0996%2012%2016.0996C13.7733%2016.0996%2015.2111%2014.6617%2015.2111%2012.8885C15.2111%2011.1152%2013.7733%209.67734%2012%209.67734ZM12%2015.2082C10.7175%2015.2082%209.68027%2014.171%209.68027%2012.8885C9.68027%2011.6059%2010.7175%2010.5687%2012%2010.5687C13.2826%2010.5687%2014.3198%2011.6059%2014.3198%2012.8885C14.3198%2014.171%2013.2826%2015.2082%2012%2015.2082Z'%20fill='%23#{iconColor(var(--company-color-brand-primary-base))}'%20stroke='%23#{iconColor(var(--company-color-brand-primary-base))}'%20stroke-width='0.2'/%3E%3Cpath%20fill-rule='evenodd'%20clip-rule='evenodd'%20d='M12.1089%201.30909H12.7689C13.4949%201.30909%2014.0889%201.89818%2014.0889%202.61818V8.78571C13.6712%208.64561%2013.2284%208.56038%2012.7689%208.53857V4.5H2.20892V13.0909H7.65537C7.64806%2013.2003%207.64434%2013.3106%207.64434%2013.4219C7.64434%2013.7569%207.67803%2014.084%207.74221%2014.4H2.20892C1.48292%2014.4%200.888916%2013.8109%200.888916%2013.0909V2.61818C0.888916%201.89818%201.48292%201.30909%202.20892%201.30909H2.86892V0H4.18892V1.30909H10.7889V0H12.1089V1.30909Z'%20fill='%23#{iconColor(var(--company-color-brand-primary-base))}'/%3E%3C/svg%3E") no-repeat !important;
                width: 18px;
                height: 19px;
            }
        }
    }
    .popover {
        z-index: 2000 !important;
        .popover-body {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            background-color: white;
            box-shadow: $box-shadow-3;
            border-radius: $border_radius_sm;
            // position: fixed !important;
            transform: translateX(0) translateY(12px) !important;
        }
        ngb-datepicker {
            border: none;
        }
        ngb-datepicker-navigation {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            ngb-datepicker-navigation-select {
                max-width: 110px;
            }
        }
        .ngb-dp-navigation-chevron {
            color: var(--company-color-brand-primary-base);
            height: 8px !important;
            width: 8px !important;
        }
        .custom-select {
            pointer-events: none !important;
            color: var(--color-neutral-gray-80) !important;
            border: none;
            height: 39px;
            outline: none;
            padding: 0;
            font-size: 14px;
            line-height: 21px;
            text-align-last: center;
            -moz-text-align-last: center;
            -ms-text-align-last: center;
            option {
                border: none !important;
                padding: 5px 0;
                outline: none;
            }
        }
        .custom-select[aria-label="Select month"] {
            text-align: right !important;
            margin-right: 5px;
        }
        .custom-select:hover {
            cursor: pointer;
        }
        .custom-select {
            -webkit-appearance: none;
            -moz-appearance: none;
            background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' height='20' viewBox='0 0 24 24' width='24'><path d='M0 0h24v24H0z' fill='none'/><path d='M12 5.83L15.17 9l1.41-1.41L12 3 7.41 7.59 8.83 9 12 5.83zm0 12.34L8.83 15l-1.41 1.41L12 21l4.59-4.59L15.17 15 12 18.17z' fill='%23#{iconColor(#ffff)}'/></svg>");
            background-repeat: no-repeat;
            background-position-x: 100%;
            background-position-y: 50%;
        }
        .ngb-dp-content {
            margin-top: 5px;
            display: flex;
            flex-direction: column;
            align-items: stretch;
            max-width: 284px;
            .ngb-dp-week {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
            }
            .ngb-dp-weekdays {
                border: none !important;
            }
            .ngb-dp-weekday {
                color: var(--company-color-brand-primary-base);
                font-style: normal;
                font-size: 14px;
                font-weight: 500;
            }
            .ngb-dp-day {
                color: var(--color-neutral-gray-50);
                font-style: normal;
                font-size: 14px;
                font-weight: 500;
                outline: none;
                padding: 4px;
                &>.text-muted {
                    display: none !important;
                }
                .bg-primary {
                    background-color: var(--company-color-brand-primary-base);
                }
                .text-white {
                    color: white;
                }
            }
        }
        ngb-timepicker {
            fieldset {
                border: none;
                color: var(--company-color-brand-primary-base);
            }
            .ngb-tp {
                button.btn-outline-primary {
                    background-color: var(--color-neutral-gray-5);
                    border: 0.8px solid var(--color-neutral-gray-20);
                    height: 40px;
                    padding: 10px 12px;
                    border-radius: $border_radius_xs;
                    color: var(--color-neutral-gray-80);
                }
            }
            .ngb-tp-input-container {
                position: relative;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                padding: 10px 0;
                padding-left: 15px;
                background-color: var(--color-neutral-gray-5);
                border: 0.8px solid var(--color-neutral-gray-20);
                border-radius: $border_radius_xs;
                .btn-link {
                    position: absolute;
                    display: flex;
                    right: 21%;
                    top: 8%;
                    outline: none !important;
                    &:first-child {
                        right: 22%;
                    }
                    &:last-child {
                        top: 44%;
                    }
                    .chevron {
                        height: 5px !important;
                        width: 5px !important;
                    }
                    .chevron::before {
                        color: var(--company-color-brand-primary-base);
                        height: 5px !important;
                        width: 5px !important;
                        border-width: 1.5px 1.5px 0px 0px !important;
                    }
                }
                .ngb-tp-input {
                    width: 50%;
                    border: none;
                    background: none;
                    text-align: left;
                    color: var(--color-neutral-gray-80);
                    font-size: 14px;
                    line-height: 14px;
                }
            }
            .sr-only {
                display: none;
            }
        }
    }
}

.buttons-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 16px;
    app-button {
        width: 48%;
    }
}