@import "../../../../styles/borders.scss";
.mat-slide-toggle {
    .mat-slide-toggle-thumb {
        border-radius: $border_radius_xs;
        background-color: var(--color-neutral-gray-20);
        height: 100%;
        margin-top: 3px;
        box-shadow: none !important;
    }
    &.mat-checked {
        .mat-slide-toggle-thumb {
            background-color: var(--company-color-brand-primary-base) !important;
        }
    }
    .mat-slide-toggle-bar {
        border-radius: $border_radius_xs;
        background-color: var(--color-neutral-gray-5) !important;
        height: 85% !important;
    }
    .mat-slide-toggle-input {
        display: none !important;
        height: 0 !important;
    }
    .cdk-visually-hidden {
        height: 0 !important;
    }
    .mat-slide-toggle-ripple {
        display: none !important;
    }
}